
'use client';

import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Eye, LogIn as LogInIcon, AlertCircle } from 'lucide-react';
import { useRouter } from 'next/navigation';
import React, { useEffect, useState } from 'react'; // Import React
import { useActionState } from 'react'; // Changed from useFormState
import { login } from '@/lib/actions';

export default function LoginPage() {
  const router = useRouter();
  const [isDebugMode, setIsDebugMode] = useState(false);

  const initialState = { message: null, errors: {} };
  // Correctly using React.useActionState
  const [state, formAction] = React.useActionState(login, initialState);


  useEffect(() => {
    // Client-side check for the environment variable
    setIsDebugMode(process.env.NEXT_PUBLIC_DEBUG_MODE === 'true');
  }, []);

  const handleDebugLogin = () => {
    // Ensure the debug_user_id matches the ID in users.json for dream_pep
    router.push('/profile?debug_user_id=dream_pep_id');
  };

  return (
    <div className="flex items-center justify-center min-h-[calc(100vh-15rem)] py-12">
      <Card className="w-full max-w-md shadow-xl">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold">登录 MineVent</CardTitle>
          <CardDescription>
            使用您的 Minecraft 用户名登录以访问活动和个人资料。
          </CardDescription>
        </CardHeader>
        <form action={formAction}>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="minecraftUsername">Minecraft 用户名</Label>
              <Input
                id="minecraftUsername"
                name="minecraftUsername"
                type="text"
                placeholder="例如：Steve"
                required
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="captcha">人机验证</Label>
              <div 
                id="captcha-placeholder" 
                className="w-full h-20 bg-muted rounded-md flex items-center justify-center text-muted-foreground text-sm"
                aria-live="polite"
              >
                人机验证区域 (例如：reCAPTCHA/Geetest)
              </div>
              {/* Hidden input for actual CAPTCHA token if integrated */}
              {/* <input type="hidden" name="captchaToken" value="mock_token_for_now" /> */}
            </div>

            {state?.message && (
              <p className="text-sm text-destructive flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message}</p>
            )}
            
            <Button type="submit" className="w-full">
              <LogInIcon className="mr-2 h-5 w-5" />
              登录
            </Button>

            {isDebugMode && (
              <Button
                type="button" // Ensure this doesn't submit the form
                variant="secondary"
                className="w-full border-dashed border-primary/50 hover:border-primary"
                onClick={handleDebugLogin}
              >
                <Eye className="mr-2 h-5 w-5" />
                调试：以 dream_pep 身份查看个人资料
              </Button>
            )}
          </CardContent>
        </form>
        <CardFooter className="flex flex-col gap-4">
          <p className="text-sm text-muted-foreground">
            遇到问题？请联系<Link href="#" className="font-medium text-primary hover:underline">管理员</Link>。
          </p>
        </CardFooter>
      </Card>
    </div>
  );
}
